import axios from "axios";
import React, { useEffect, useState } from "react";
import { useLocation } from "react-router";
import Navbar from "./Narbabr";
import "./singlepost.css";
import SinSecondPart from "./SinSecondPart";
import YouTube from "react-youtube";

const SinglePost = () => {
  const Location = useLocation();
  const path = Location.pathname.split("/")[2];
  const [data, setData] = useState([]);
  const [genres, setgenres] = useState([]);
  const [crew, setcrew] = useState([]);
  const [youtube, setyoutube] = useState([]);
  const [play, setplay] = useState(false);

  console.log(path);
  useEffect(() => {
    const moviesearch = () => {
      axios
        .get(
          `https://api.themoviedb.org/3/movie/${path}?api_key=9d857b7a8c2c0bb9eac30a054ec63ac1&append_to_response=credits,videos`
        )
        .then((res) => {
          console.log(res.data);
          setData(res.data);
          setgenres(res.data.genres);
          setcrew(res.data.credits.cast);
          setyoutube(res.data.videos);
        });
    };
    moviesearch();
  }, [path]);
  const time_convert = (num) => {
    var hours = Math.floor(num / 60);
    var minutes = num % 60;
    return hours + "h" + " " + minutes + "m";
  };
  const Image_Path = "https://www.themoviedb.org/t/p/original";
  const poster_path = "https://www.themoviedb.org/t/p/original";
  const iconsvg =
    "https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-175-play-806cb05551791b8dedd7f8d38fd3bd806e2d397fcfeaa00a5cc9129f0819fd07.svg";

  const trailer = () => {
    const trl = data.videos.results[0].key;
    return <YouTube 
             videoId={trl}
            className={"youtube-container"}
            
            opts={{
              width:"100%",
              height:"50%"
            }}
    
          />;
  };

  return (
    <div className="poster">
      <Navbar />
      <div
        className="single_poster"
        style={{
          backgroundImage: `url('${Image_Path}${data.backdrop_path}')`,
          // height: "50vh",/
          // width: "50%",
        }}
      >

        <div className="poster_inner_container">
        
          <div className="poster_image_container">
            <img
              key={data.id}
              className="poster_image"
              src={`${poster_path}${data.poster_path}`}
              alt=""
            />
            
          </div>
          
          <div className="poster_details">

            <div className="details_title">
              <h2 className="details_title_t">{data.title}</h2>
            </div>
            <div className="poster_facts">
              <div className="poster_release_Date">{data.release_date}</div>
              <div className="poster_genres">
                <p className="dought">.</p>
                {genres.map((data) => {
                  return (
                    <p key={data.id} className="poster_name">
                      {data.name},
                    </p>
                  );
                })}
              </div>
              <p className="dought">.</p>
              <div className="poster_time">{time_convert(data.runtime)}</div>
            </div>
            <div className="svgactions">
          {data.videos && play ? trailer() : null}

              <li className="svglogo">
                <img
                  className="svgicon"
                  src="https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-159-thumbnails-list-white-c260ea972eebf812289fd3c41d0d2c1dff33ecbcd62be13fba8eeaf9de173789.svg"
                  alt=""
                />{" "}
              </li>
              <h5 className="Add">Add to list</h5>
              <li className="svglogo">
                <img
                  className="svgicon"
                  src="https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-13-heart-white-28d2cc2d6418c5047efcfd2438bfc5d109192671263c270993c05f130cc4584e.svg"
                  alt=""
                />
              </li>
              <h5 className="heart">Mark as Favorite</h5>

              <li className="svglogo">
                <img
                  className="svgicon"
                  src="https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-73-bookmark-white-432e98d550b7e4c80b06272c49475b0db85a60f6fae450420713004b3c9d3ffd.svg"
                  alt=""
                />
              </li>
              <h5 className="Watch">Add to your Wacthlist</h5>

              <li className="svglogo">
                <img
                  className="svgicon"
                  src="https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-49-star-white-5c85220678b312aea9599d5f12ad858a9e7df226de51ef8b6b699023ffeda5fa.svg"
                  alt=""
                />
              </li>
              <h5 className="rate"> Rate It!</h5>
              {play?<button className="close--btn" onClick={()=>{setplay(false)}} >close</button>:null}
              <p
                onClick={() => {
                  setplay(true);
                }}
                // onDoubleClick={() => {
                //   setplay(false);
                // }}
                className="play_button"
              >
              <p className="playicon" ></p>
              <h6 className="Trailer">Play Trailer</h6>
              </p>
            </div>
            <div className="poster_tagline">
              <p className="tagline">{data.tagline}</p>
            </div>
            <div className="poster_overview">
              <h3 className="overview_headline">Overview</h3>
              <p className="overview_data">{data.overview}</p>
            </div>
          </div>
        </div>
      </div>
      <div>
        <SinSecondPart crew={crew} youtube={youtube} data={data} />
      </div>
    </div>
  );
};

export default SinglePost;
