import React from "react";
import "../Body.css";
import { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";


const ForRent = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get(
        "https://api.themoviedb.org/3/movie/top_rated?api_key=9d857b7a8c2c0bb9eac30a054ec63ac1&language=en-US&page=1"
      )
      .then((res) => {
        // console.log(res?.data?.results);
        setData(res?.data?.results);
      });
  }, []);

  const getimage = (posterpath) => {
    return `https://www.themoviedb.org/t/p/w220_and_h330_face${posterpath}`;
  };
  return (

    <div className="movie_card">
      {data.map((data) => {
        return (
          <div className="Card" key={data.id}>
          <Link  to={`/movieverse/${data.id}`} >
            <img
              src={getimage(data.poster_path)}
              // alt="Download image"
              className="card_image"
            />
            </Link>
            <p className="card_title">{data.title}</p>
            <p className="card_date">{data.release_date}</p>
          </div>
        );
      })}
    </div>
  );
};

export default ForRent;
