import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const Popular = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get(
        "https://api.themoviedb.org/3/tv/on_the_air?api_key=9d857b7a8c2c0bb9eac30a054ec63ac1&language=en-US&page=1"
      )
      .then((res) => {
        // console.log(res?.data?.results);
        setData(res?.data?.results);
      });
  }, []);

  const getimage = (posterpath) => {
    return `https://www.themoviedb.org/t/p/w220_and_h330_face${posterpath}`;
  };

  return (
    <div className="movie_card">
      {data.map((data) => {
        return (
          <div className="Card" key={data.id}>
          <Link to={`/movieverse/${data.id}`} >
            <img src={getimage(data.poster_path)} className="card_image" />
            </Link>
            <p className="card_title">{data.name}</p>
            <p className="card_date">{data.first_air_date}</p>
          </div>
        );
      })}
    </div>
  );
};

export default Popular;


