import "./App.css";
import { Routes, Route } from "react-router-dom";
import Home from "./components/Home";
import 'bootstrap/dist/css/bootstrap.css';
import '../node_modules/bootstrap/dist/js/bootstrap.bundle';
import SinglePost from "./components/SinglePost";
import { useEffect, useState } from "react";
import axios from "axios";


function App() {
  const [inputfiled , setInputfiled] = useState('')
  const [data , setData] = useState([])
 const [ btn , setbtn] = useState("race")

  const inputChange=(e)=>{
     const data = e.target.value
     setInputfiled(data)
  }
  const btnADD=()=>{
   setbtn(inputfiled)
 }

  useEffect(()=>{
     axios.get(`https://api.themoviedb.org/3/search/movie?api_key=9d857b7a8c2c0bb9eac30a054ec63ac1&language=en-US&query=${btn}&page=1&include_adult=false`).then((res)=>{
         // console.log(res?.data?.results);
         setData(res?.data?.results)   
     })
  },[btn])

  return (
    <div className="App">
     <Routes>
      <Route exact path="/movieverse"  element={<Home data={data} inputChange={inputChange} btnADD={btnADD} btn={btn} />} />
      <Route exact path="/movieverse/:id" element={<SinglePost/>}/>
     </Routes>
    </div>
  );
}

export default App;
