import React from 'react'
import "./searchingmovie.css"
import { Link} from "react-router-dom";

const Searchingmovie = ({data,btn}) => {
    const getimage = (posterpath) => {
      return `https://www.themoviedb.org/t/p/w220_and_h330_face${posterpath}`;
    };
    
  return (
    <div className='searchmoviecontianer'>
     <div className='NAVABR'>
     </div>
        <div className='searchmovie_top'>
          <div className='searchmovie_search_results'>
            <h2>Search Results</h2>
          </div>
          <div>
            <h2 className='search_name'> {btn.toUpperCase()} </h2>
          </div>
        </div>
        <div className='searchmovie_bottom'>
        {data?.map((data)=>{
          return(
            <div className='search_card' key={data.id}>
            <Link to={`/movieverse/${data.id}`}>
            <img src={getimage(data.poster_path)}  className="search_image" alt="" />
            </Link>
             
              <p className="search_title">{data.title}</p>
              <p className="search_date">{data.release_date}</p>
          </div>
          )
          })}
        </div>
    </div>
  ) 
}

export default Searchingmovie