import React from "react";
import "./SinSecondPart.css";

const SinSecondPart = ({ crew , youtube, data}) => {


   const getimage = "https://www.themoviedb.org/t/p/w138_and_h175_face"
   const defaultimage = "https://www.pngfind.com/pngs/m/467-4675403_png-file-blank-person-transparent-png.png"


  return (
    <div className="SinSecondPart_container">
      <div className="SinSecondPart_left">
        <h5 className="topbilledcast">Top Billed Cast</h5>
        <div className="person_container is_hidden">
          {crew.map((data) => {
            return (
              <div className="person_card" key={data.id}>
                <img
                className="person_image"
                  src={ data.profile_path ?  `${getimage}${data.profile_path}` : `${defaultimage}`}
                  alt="loading......"
                />
                <h6>{data.name}</h6>
                <h6>{data.character}</h6>
              </div>
            );
          })}
        </div>
        <div>
        


        </div>
      </div>
      <div className="SinSecondPart_right">
      </div>
    </div>
  );
};

export default SinSecondPart;
