import React from "react";
import "./home.css"
import Body from "./Body";
import Navbar from "./Narbabr";


const Home = ({btnADD , inputChange , data ,btn})=>{
  
    return(
        <div className="container_1" >
           <Navbar/>
           <div className="mid_contianer">
               <div className="mid_content_wrapper">
                <div className="title">welcome.</div>
                <div className="title_2">Millions of movies, TV shows and people to discover.Explore now.</div>
                <div className="search">
                  <input type="text" className="inputtext" onChange={inputChange} placeholder="Search...... "/>
                  <button type="button" onClick={btnADD} className="btn_search">Search</button>
                </div>
                </div>
           </div>
           <div>
              <Body data={data} btn={btn}/>
           </div>

        </div>
    )
}
export default Home;