import React from "react";
import Popular from "./tvshows/Popular";
import Top_rated from './tvshows/Top_rated'

const Bodyfreewatch = () => {
  return (
    <div>
      <div className="second_contianer">
        <h2>Free To Watch</h2>
        <ul className="nav nav-pills mb-3 free_to_watch_2" id="pills-tab">
          <li className="nav-item">
            <p
              className="free_btn active"
              id="pills-profilefree-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-profilefree"
            >
              Popular
            </p>
          </li>
          <li className="nav-item">
            <p
              className="free_btn free_btn_2"
              id="pills-contactfree-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-contactfree"
            >
              Top-Rated
            </p>
          </li>
        </ul>
        <div
          className="tab-content container_free_watch "
          id="pills-tabContent"
        >
          <div
            className="tab-pane fade show active container_profile"
            id="pills-profilefree"
          >
            <div className="movie_card">
              <Popular />
            </div>
          </div>
          <div className="tab-pane fade container_profile" id="pills-contactfree">
            <div className="movie_card">
              <Top_rated />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Bodyfreewatch;
