import React from "react";
import "./Body.css";
import Ontv from "./whatspopular/Ontv";
import Streaming from "./whatspopular/Streaming";
import ForRent from "./whatspopular/ForRent";
import Bodyfreewatch from "./Bodyfreewatch";
import Searchingmovie from "./Searchingmovie";
// import { useState } from "react";



const Body = ({data,btn}) => {
  // console.log(data);
  // const [active , setActive] = useState(false)

  return (
    <div className="body_card">
         <Searchingmovie data={data} btn={btn}/>
      <div className="second_contianer">
        <h2>What's Popular</h2>
        <ul className="nav  mb-3 free_to_watch" id="pills-tab" >
          <li className="nav-item">
            <p
              className="free_btn active"
              id="pills-profile-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-profile" 
            >
              Streaming
            </p>
          </li>
          <li className="nav-item">
            <p
              className="free_btn free_btn_2"
              id="pills-contact-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-contact"
            >
              On TV
            </p>
          </li>
          <li className="nav-item">
          <p
              className="free_btn free_btn_2"
              id="pills-home-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-home"
            >
              
              For Rent
            </p>
          </li>
          
        </ul>
        <div className="tab-content container_free_watch " id="pills-tabContent">
          <div className="tab-pane fade show active container_profile" id="pills-profile"  >
              <div className="movie_card">    
              <Streaming/>                
              </div>
          </div>
          <div className="tab-pane fade container_profile"
            id="pills-contact"
            >
           <div className="movie_card">    
              <Ontv/>                
              </div>
          </div>
          <div className="tab-pane fade container_profile"
            id="pills-home"
            >
            <div className="movie_card">    
              <ForRent/>                
              </div>
          </div>
        </div>
      </div>
        <Bodyfreewatch/>
    </div>
  );
};

export default Body;
