import React from "react";
import './Navbar.css'
import logo from "./images/logoTMDB.svg"
import bell from "./images/bellicon.png"
import avtar from "./images/avtaricon.png"
import search from "./images/searchicon.png"
import plus from "./images/plusicon.png"
import eng from "./images/engiocn.png"

const Navbar = () => {
  return (
   
      <div className="header">
        <div className="header_left">
          <img src={logo} className="logo" alt="" />
          <li className="li">Movies</li>
          <li className="li">TVShows</li>
          <li className="li">People</li>
          <li className="li">More</li>
        </div>
        <div className="header_right">
          <img src={plus} className="logo_right white_right" alt="" />
          <img src={eng} className="logo_right" alt="" />
          <img src={bell} className="logo_right" alt="" />
          <img src={avtar} className="logo_right avtar" alt="" />
          <img src={search} className="logo_right" alt="" />
        </div>
      </div>
  );
};

export default Navbar;
